import {
  Scene,
  Group,
  PerspectiveCamera,
  Color,
  AmbientLight,
  DirectionalLight,
  WebGLRenderer,
  LoadingManager,
  TextureLoader,
  MeshPhongMaterial,
  Mesh,
  PlaneGeometry,
  MeshBasicMaterial,
  DoubleSide
} from '../build/three.min-custom-jorre';




import { RoundedBoxGeometry } from './RoundedBoxGeometry.js';

export class Models3D {
  constructor(container, isHorizontal) {
    this.container = container;

    this.index = 0;
    this.flipped = false;
    this.moveDistance = 1.5;

    this.models = [];
  }

  addModel(dimensions, color, frontTexture, backTexture) {
    const dimensionsArray = dimensions.split(",");

    const realWidth = Number(dimensionsArray[0]);
    const realHeight = Number(dimensionsArray[1]);
    const realDepth = Number(dimensionsArray[2]);

    const model = {
      width: Number(dimensionsArray[0]),
      height: Number(dimensionsArray[1]),
      depth: Number(dimensionsArray[2]),
      color: color,
      frontTexture: frontTexture,
      backTexture: backTexture,
    }

    this.models.push(model);
  }

  init(callback, x, y, z, isHorizontal, debug) {
    this.callback = callback;
    this.isHorizontal = isHorizontal;

    this.initScene(x, y, z);
    this.addObjects(x, y, z, debug);
    this.animate();
  }

  initScene(x, y, z) {
    this.group = new Group();

    let camera = new PerspectiveCamera(50, 10, 0.1, 10);
    camera.position.z = z;

    let scene = new Scene();
    scene.background = new Color(0xffffff);

    const ambientLight = new AmbientLight(0xffffff, .9);
    
  	scene.add( ambientLight );

    const light = new DirectionalLight(0xAAAAAA, .3);
    
    light.position.set(-.35 * z + x, -.3 * z + y, z * .9);
    scene.add(light);

  	scene.add( camera );

    camera.aspect = this.container.offsetWidth / this.container.offsetHeight;
  	camera.updateProjectionMatrix();

  	let renderer = new WebGLRenderer();

  	renderer.setPixelRatio( window.devicePixelRatio );
  	renderer.setSize( this.container.offsetWidth, this.container.offsetHeight );
  	this.container.appendChild( renderer.domElement );

    this.scene = scene;
    this.camera = camera;
    this.renderer = renderer;
  }

  addObjects(x, y, z, debug) {
    this.objects = [];

    const manager = new LoadingManager();
    manager.onLoad = this.objectLoadHandler.bind(this);

    const loader = new TextureLoader(manager);

    const shininess = 80;
    const specular = 0x333333;

    for (var i = 0; i < this.models.length; i++) {
      var model = this.models[i];

      const materials = [
        new MeshPhongMaterial({color: model.color, transparent:true, shininess: shininess, specular: specular}), //RIGHT
        new MeshPhongMaterial({color: model.color, transparent:true, shininess: shininess, specular: specular}), //LEFT
        new MeshPhongMaterial({color: model.color, transparent:true, shininess: shininess, specular: specular}), //TOP
        new MeshPhongMaterial({color: model.color, transparent:true, shininess: shininess, specular: specular}), //BOTTOM
        new MeshPhongMaterial({map: loader.load(model.frontTexture), transparent:true, shininess: shininess, specular: 0x888888}), //FRONT
        new MeshPhongMaterial({map: loader.load(model.backTexture), transparent:true, shininess: 30, specular: 0x111111}), //BACK
      ];

      const width = 1 / model.height * model.width;
      const height = 1;
      const depth = 1 / model.height * model.depth;

      const depthFactor = 1.6;//1.2;
      const geometry = new RoundedBoxGeometry(width, height, depth * depthFactor, 10, .3 );

      let mesh = new Mesh(geometry, materials);
      mesh.visible = false;
      mesh.scale.set( 1, 1, 1 / depthFactor );

      //setOpacity(mesh, 0);

      this.group.add(mesh);
      this.objects.push(mesh);
    }

    this.scene.add(this.group);

    this.group.position.x = x;
    this.group.position.y = y;

    if (debug) {
      const geo = new PlaneGeometry( 1, 1, 1 );
      const mat = new MeshBasicMaterial( {color: 0xffff00, side: DoubleSide} );
      const plane = new Mesh( geo, mat );
      this.group.add( plane );
    }
  }

  objectLoadHandler() {
    this.callback();
  }

  showObject(index, direction) {
    this.isTweening = true;

    if (this.flipped) {
      let object = this.objects[this.index];
      TweenMax.to(object.rotation, .8, {onComplete: this.continueObject.bind(this), onCompleteParams: [index, direction], ease: Power3.easeInOut, y: 0});
    }else{
      this.continueObject(index, direction);
    }

    this.flipped = false;
  }

  continueObject(index, direction) {
    let speed = 1.25;
    let sign = 1;
    if (direction == 'back')
      sign = -1;

    //new object
    let object = this.objects[index];
    object.visible = true;
    object.rotation.y = -Math.PI * 1.5 * sign;
    if (this.isHorizontal) {
      object.position.x = this.moveDistance * sign;
      TweenMax.to(object.position, speed + .1, {delay: 0, ease: Power3.easeOut, x: 0});
    }else{
      object.position.y = this.moveDistance * sign;
      TweenMax.to(object.position, speed + .1, {delay: 0, ease: Power3.easeOut, y: 0});
    }

    TweenMax.to(object.rotation, speed, {onComplete: this.showObjectHandler.bind(this), delay: .1, ease: Power3.easeOut, y: 0});

    //old object
    object = this.objects[this.index];
    if (this.isHorizontal) {
      TweenMax.to(object.position, speed, {ease: Power3.easeOut, x: -this.moveDistance * sign});
    }else{
      TweenMax.to(object.position, speed, {ease: Power3.easeOut, y: -this.moveDistance * sign});
    }

    TweenMax.to(object.rotation, speed, {onComplete: this.hideObjectHandler.bind(this), onCompleteParams: [object], ease: Power3.easeOut, y: Math.PI * .5 * sign});

    this.index = index;
  }

  hideObjectHandler(object) {
    object.visible = false;
  }

  showObjectHandler() {
    this.isTweening = false;
  }

  flipObject() {
    if (!this.isTweening) {
      this.isTweening = true;
      let object = this.objects[this.index];

      let angle = 0
      if (!this.flipped) {
        angle = Math.PI;
      }

      TweenMax.to(object.rotation, .8, {onComplete: this.flipObjectHandler.bind(this), ease: Power3.easeInOut, y: angle});
      this.flipped = !this.flipped;
    }
  }

  flipObjectHandler() {
    this.isTweening = false;
  }

  show() {
    var object = this.objects[0];
    object.visible = true;
    object.position.x = 0;

    this.isTweening = true;

    TweenMax.from(document.querySelector('.container3D'), .6, {ease: Sine.easeIn, alpha: 0});

    TweenMax.from(object.position, 3, {ease: Sine.easeOut, z: 1.6, y: -1});
    TweenMax.from(object.rotation, 3, {onComplete: this.showHandler.bind(this), y: Math.PI * 2});
  }

  showHandler() {
    this.isTweening = false;
  }

  animate() {
    requestAnimationFrame( this.animate.bind(this) );
    this.renderer.render( this.scene, this.camera );
  }

}
